
.publicidad {
    width: 295px;
    height: auto;
    float: left;
    margin-left: 6px;
}

.publicidad a img {
    width: 280px;
    height: 200px;
    margin: 0 0 10px 0;
    background-color: #ccc;
}

.contenidointerno {
    width: 609px;
    height: auto;
    margin: 0;
    float: right;
}

.contenidointerno h1 {
    font-style: normal;
    font-weight: bold;
    color: #DC1F26;
    text-decoration: none;
    display: block;
}

.contenidointerno h4 {
    font-size: 13px !important;
    font-style: normal;
    font-weight: bold;
    color: #414141;
    text-decoration: none;
    display: block;
}

.clearbox {
    clear: both;
}

.datoscine {
    font-size: 12px;
    margin-bottom: 0em;
    margin-top: 0.8em !important;
    color: #666666;
    line-height: 15px;
    text-decoration: none;
}
.contenidocine {
    width:250px;
    height:350px;
    margin-top:10px;
    margin-right:50px;
    padding:10px;
    float:left;  
    display:inline;
    background-color:#EEE;
    border:1px solid #cccccc;
}

.cineinfo {
    background-color: #FFF;
    width: 600px;
    height: auto;
}

.cineinfo_datos {
    width: 55%;
}

.filaimpar {
    background-color: rgb(255, 253, 221);
}

.celdatitulo {
    width : 80%;
    font: 12px Arial, Helvetica, sans-serif;
    font-style: normal;
    color: #666;
    cursor: pointer;
    line-height: 17px;
}

.celda {
    font: 12px Arial, Helvetica, sans-serif;
    color: #666;
    cursor: pointer;
    float: right;
    margin-top: -15px;
    line-height: 17px;
}

.cineinfo p {
	text-align: justify;
}

.aviso p {
	font-size: 10px;
}

.cineinfo img {
    float : right;
    margin: -265px 5px 0 0;
}

.cineinfo_peliculas {
	width: 600px;
}

.celdacabecera {
    display: inline-block;
	width: 50%;
	border: 0;
    border-bottom: 1px;
    border-color: #FFFAB4;
    border-style: solid;
    color: #FFFFFF;
    background-color: #000;
    font-size: 11px;
    font-family: "Trebuchet MS",Arial,Helvetica,sans-serif;
    font-weight: bold;
    margin-right: -4px;
}
.datospelicula .tabla {
    display: table;
    font-size: 12px;
    margin-bottom: 0em;
    margin-top: 0.8em !important;
    color: #666666;
    line-height: 15px;
    text-decoration: none;
}

.datospelicula .fila {
    display: table-row;
}

.datospelicula .celdatitulo {
    font-weight: bold;
    height: 25px;
}

.datospelicula .celda {
    display: table-cell;
    width: 80%;
}

.peliculavideo {
    margin-top: 70px;
}
